@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');

#root, body, html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    height: 100vh;
    font-family: 'Indie Flower', cursive;
}

.container{
    height: 100%;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card{
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
    border-radius: 25px;
    padding: 2%;
    position: relative;
}

.button{
    width: 25%;
    height: 45px;
    border-radius: 10px;
    border: 1px black solid;
    background-color: aliceblue;
    cursor: pointer;
    font-weight: bold;
    font-size: 16px;
}

.button:hover{
    background-color: black;
    color: aliceblue;
    border: 1px aliceblue solid;
}

.background-image-0{
    background: url('../public/images/image_1.jpg');
    background-size: cover;
}

.background-image-1{
    background: url('../public/images/image_2.jpg');
    background-size: cover;
}

.background-image-2{
    background: url('../public/images/image_3.jpg');
    background-size: cover;
}

.background-image-3{
    background: url('../public/images/image_4.jpg');
    background-size: cover;
}

.background-image-4{
    background: url('../public/images/image_5.jpg');
    background-size: cover;
}

.background-image-5{
    background: url('../public/images/image_6.jpg');
    background-size: cover;
}

.whatsapp-image, .twitter-image{
    width: 20px;
    height: 20px;
}

.whatsapp-share-btn{
    background-color: transparent;
    border: none;
    width: 20px;
    height: 20px;
    position: absolute;
    right: 70px;
    top: 20px;
    cursor: pointer;
}

.twitter-share-btn{
    background-color: transparent;
    border: none;
    width: 20px;
    height: 20px;
    position: absolute;
    right: 40px;
    top: 20px;
    text-decoration: none;
    cursor: pointer;
}

.twitter-icon{
    color: #3b95b2
}

.loading-container{
    width: 100px;
    height: 110px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.loading-image{
    width: 50px;
    height: 50px;
}
